import React, { useEffect, useState } from 'react';
import { useUser } from '@clerk/clerk-react';
import { useNavigate } from 'react-router-dom';
import { collection, query, where, getDocs, deleteDoc, doc } from 'firebase/firestore';
import { db } from '../firebase';
import Colors from '../colors';

const Inbox = () => {
  const { user } = useUser();
  const navigate = useNavigate();
  const [userList, setUserList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [filteredList, setFilteredList] = useState([]);

  useEffect(() => {
    if (user) {
      getUserList();
    }
  }, [user]);

  useEffect(() => {
    if (userList.length > 0) {
      const filtered = filterUserList();
      setFilteredList(filtered);
    }
  }, [userList]);

  //  Tm sohbetleri Firebase'den sil
  const deleteAllChats = async () => {
    if (!window.confirm('Tm sohbetleri silmek istediinizden emin misiniz?')) {
      return;
    }

    try {
      console.log(" Tm sohbetler siliniyor...");
      const allChatsQuery = query(collection(db, 'Chat'));
      const allChatsSnapshot = await getDocs(allChatsQuery);

      console.log(" Silinecek sohbet says:", allChatsSnapshot.size);

      for (const chatDoc of allChatsSnapshot.docs) {
        console.log(" Sohbet siliniyor:", chatDoc.id);
        await deleteDoc(doc(db, 'Chat', chatDoc.id));
      }

      console.log(" Tm sohbetler silindi!");
      setUserList([]);
      setFilteredList([]);
      alert('Tm sohbetler baaryla silindi!');
    } catch (error) {
      console.error(" Sohbet silme hatas:", error);
      alert('Sohbetler silinirken bir hata olutu.');
    }
  };

  // Kullanc listesinin alnmas mevcut kullanc e-postalarna baldr
  const getUserList = async () => {
    setLoading(true);
    setUserList([]);

    const currentUserEmail = user?.primaryEmailAddress?.emailAddress;
    console.log(" Inbox ykleniyor - Kullanc email:", currentUserEmail);

    if (!currentUserEmail) {
      console.error(" Kullanc email'i bulunamad");
      setLoading(false);
      return;
    }

    try {
      // Firebase'den bu kullancnn dahil olduu tm sohbetleri al
      console.log(" Firebase query: userIds array-contains", currentUserEmail);
      const q = query(collection(db, 'Chat'),
        where('userIds', 'array-contains', currentUserEmail));
      const querySnapshot = await getDocs(q);

      console.log(" Firebase'den dnen sohbet says:", querySnapshot.size);

      if (querySnapshot.size === 0) {
        console.log(" Bu kullanc iin hibir sohbet bulunamad!");
        console.log(" Kontrol edilecek email:", currentUserEmail);
      }

      const chatList = [];
      querySnapshot.forEach((doc) => {
        const chatData = doc.data();
        console.log("\n Bulunan sohbet:");
        console.log(" Document ID:", doc.id);
        console.log(" UserIds array:", chatData.userIds);
        console.log(" Users array:", chatData.users?.map(u => ({ email: u.email, name: u.name })));
        console.log(" Array contains check:", chatData.userIds?.includes(currentUserEmail));

        chatList.push({
          id: doc.id,
          ...chatData
        });
      });

      console.log(" Toplanan sohbet listesi:", chatList.length, "adet");
      setUserList(chatList);
    } catch (error) {
      console.error(" Sohbet listesi ykleme hatas:", error);
    } finally {
      setLoading(false);
    }
  };

  // Dier kullanclarn listesini filtrele
  const filterUserList = () => {
    const list = [];
    const currentUserEmail = user?.primaryEmailAddress?.emailAddress;

    console.log(" Filtreleme balyor - Mevcut kullanc:", currentUserEmail);
    console.log(" Toplam ilenecek sohbet:", userList.length);

    userList.forEach((record, index) => {
      console.log(`\n--- Sohbet ${index + 1} ileniyor ---`);
      console.log(" Record ID:", record.id);
      console.log(" UserIds:", record.userIds);
      console.log(" Users array:", record.users);

      // userIds kontrol
      if (!record.userIds || !Array.isArray(record.userIds)) {
        console.warn(" userIds eksik veya geersiz");
        return;
      }

      // users kontrol
      if (!record.users || !Array.isArray(record.users)) {
        console.warn(" users array eksik veya geersiz");
        return;
      }

      // Dier kullancy bul
      const otherUser = record.users.find((u) =>
        u && u.email && u.email !== currentUserEmail
      );

      console.log(" Bulunan dier kullanc:", otherUser);

      if (otherUser) {
        const result = {
          docId: record.id,
          email: otherUser.email,
          name: otherUser.name || otherUser.email?.split('@')[0] || 'Kullanc',
          imageUrl: otherUser.imageUrl || '',
          role: otherUser.role || 'user',
          petId: otherUser.petId || '',
          petName: otherUser.petName || ''
        };

        console.log(" Listeye eklenen kullanc:", result);
        list.push(result);
      } else {
        console.warn(" Bu sohbette dier kullanc bulunamad");
      }
    });

    console.log(" Final filtrelenmi liste:", list.length, "adet");
    return list;
  };

  const navigateToChat = (chatId) => {
    navigate(`/chat/${chatId}`);
  };

  const UserItem = ({ userInfo }) => {
    console.log("UserItem received:", userInfo);

    if (!userInfo) {
      return (
        <div style={{
          padding: '15px',
          backgroundColor: '#f9f9f9',
          borderRadius: '8px',
          margin: '10px'
        }}>
          <p>Invalid user data</p>
        </div>
      );
    }

    // Get initials if no profile image
    const getInitials = () => {
      if (!userInfo.name) return "?";
      return userInfo.name.split(' ')
        .map(word => word.charAt(0))
        .join('')
        .toUpperCase()
        .substring(0, 2);
    };

    // Format email to show only the username part before @
    const formatEmail = (email) => {
      if (!email) return '';
      const parts = email.split('@');
      return parts[0];
    };

    return (
      <div
        onClick={() => navigateToChat(userInfo.docId)}
        style={{
          padding: '15px 20px',
          backgroundColor: 'white',
          borderBottom: '1px solid #f0f0f0',
          cursor: 'pointer',
          transition: 'background-color 0.2s',
          ':hover': {
            backgroundColor: '#f8f9fa'
          }
        }}
        onMouseEnter={(e) => e.target.style.backgroundColor = '#f8f9fa'}
        onMouseLeave={(e) => e.target.style.backgroundColor = 'white'}
      >
        <div style={{
          display: 'flex',
          alignItems: 'center'
        }}>
          {userInfo.imageUrl ? (
            <img
              src={userInfo.imageUrl}
              alt={userInfo.name}
              style={{
                width: '50px',
                height: '50px',
                borderRadius: '25px',
                objectFit: 'cover'
              }}
            />
          ) : (
            <div style={{
              width: '50px',
              height: '50px',
              borderRadius: '25px',
              backgroundColor: Colors.PRIMARY,
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              color: 'white',
              fontSize: '18px',
              fontWeight: 'bold'
            }}>
              {getInitials()}
            </div>
          )}

          <div style={{
            flex: 1,
            marginLeft: '15px'
          }}>
            <h4 style={{
              margin: 0,
              fontSize: '16px',
              fontWeight: 'bold',
              color: '#333'
            }}>
              {userInfo?.name || formatEmail(userInfo?.email) || 'Unknown User'}
            </h4>
          </div>

          <div style={{
            fontSize: '22px',
            color: '#ccc'
          }}>
            
          </div>
        </div>
      </div>
    );
  };

  return (
    <div style={{
      maxWidth: '800px',
      margin: '0 auto',
      backgroundColor: '#fff',
      minHeight: '100vh'
    }}>
      {/* Clean header design */}
      <div style={{
        padding: '20px',
        backgroundColor: '#fff',
        borderBottom: '1px solid #f0f0f0'
      }}>
        <div style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between'
        }}>
          <div style={{
            display: 'flex',
            alignItems: 'center',
            gap: '15px'
          }}>
            <h1 style={{
              fontSize: '24px',
              fontWeight: 'bold',
              color: '#333',
              margin: 0
            }}>
              Sohbetler
            </h1>
            {filteredList.length > 0 && (
              <div style={{
                backgroundColor: Colors.PRIMARY,
                color: 'white',
                borderRadius: '12px',
                padding: '4px 8px',
                fontSize: '14px',
                fontWeight: 'bold'
              }}>
                {filteredList.length}
              </div>
            )}
          </div>

          {/*  Temizle butonu */}
          <button
            onClick={deleteAllChats}
            style={{
              backgroundColor: '#ff4444',
              color: 'white',
              border: 'none',
              padding: '8px 12px',
              borderRadius: '8px',
              fontSize: '12px',
              fontWeight: 'bold',
              cursor: 'pointer'
            }}
          >
             Temizle
          </button>
        </div>
      </div>

      {/* Content */}
      {loading ? (
        <div style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          padding: '60px 20px',
          textAlign: 'center'
        }}>
          <div style={{
            width: '40px',
            height: '40px',
            border: `4px solid ${Colors.PRIMARY}`,
            borderTop: '4px solid transparent',
            borderRadius: '50%',
            animation: 'spin 1s linear infinite',
            marginBottom: '20px'
          }} />
          <p style={{
            fontSize: '16px',
            color: '#666'
          }}>Sohbetler ykleniyor...</p>
        </div>
      ) : (
        <div>
          {filteredList.length === 0 ? (
            <div style={{
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              justifyContent: 'center',
              padding: '60px 40px',
              textAlign: 'center'
            }}>
              <div style={{
                width: '100px',
                height: '100px',
                borderRadius: '50px',
                backgroundColor: Colors.PRIMARY,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                marginBottom: '20px',
                fontSize: '40px'
              }}>
                
              </div>
              <h2 style={{
                fontSize: '18px',
                fontWeight: 'bold',
                marginBottom: '10px',
                color: '#333'
              }}>Henz sohbetiniz yok</h2>
              <p style={{
                fontSize: '16px',
                color: '#666',
                lineHeight: '1.5',
                maxWidth: '400px'
              }}>
                Pet sahipleriyle iletiime geerek bir sohbet balatabilirsiniz
              </p>
            </div>
          ) : (
            <div>
              {filteredList.map((item) => (
                <UserItem key={item.docId || Math.random().toString()} userInfo={item} />
              ))}
            </div>
          )}
        </div>
      )}

      <style>
        {`
                    @keyframes spin {
                        0% { transform: rotate(0deg); }
                        100% { transform: rotate(360deg); }
                    }
                `}
      </style>
    </div>
  );
};

export default Inbox;
