import React from 'react';
import { useUser } from '@clerk/clerk-react';
import { Link } from 'react-router-dom';
import PetListByCategory from '../components/PetListByCategory';
import Colors from '../colors';

const Home = () => {
    const { user, isLoaded } = useUser();

    if (!isLoaded) {
        return <div className="loading" style={{ textAlign: 'center', padding: '50px' }}>Loading...</div>;
    }

    return (
        <div className="home-container">
            {/* Main Content */}
            <div style={{
                maxWidth: '1200px',
                margin: '0 auto',
                padding: '40px 20px'
            }}>
                {/* Welcome Section */}
                <div style={{
                    marginBottom: '40px',
                    backgroundColor: Colors.LIGHT_PRIMARY,
                    padding: '30px',
                    borderRadius: '12px',
                    textAlign: 'center'
                }}>
                    <h1 style={{
                        fontSize: '28px',
                        fontWeight: 'bold',
                        marginBottom: '15px',
                        color: '#333'
                    }}>
                        Welcome, {user?.firstName || user?.emailAddresses?.[0]?.emailAddress?.split('@')[0] || 'Guest'}
                    </h1>
                    <p style={{
                        color: Colors.GRAY,
                        fontSize: '18px',
                        maxWidth: '600px',
                        margin: '0 auto'
                    }}>
                        Find your perfect pet companion. Browse through our available pets and give them a loving home.
                    </p>
                </div>

                {/* Pet List by Category */}
                <PetListByCategory />

                {/* Add New Pet Button */}
                <div style={{
                    display: 'flex',
                    justifyContent: 'center',
                    marginTop: '60px',
                    marginBottom: '30px'
                }}>
                    <Link
                        to="/add-pet"
                        style={{
                            display: 'flex',
                            flexDirection: 'row',
                            gap: '10px',
                            alignItems: 'center',
                            justifyContent: 'center',
                            padding: '15px 30px',
                            backgroundColor: Colors.LIGHT_PRIMARY,
                            color: Colors.PRIMARY,
                            borderRadius: '8px',
                            border: `2px dashed ${Colors.PRIMARY}`,
                            textDecoration: 'none',
                            fontWeight: '600',
                            fontSize: '16px',
                            width: '450px',
                            maxWidth: '95%',
                            transition: 'all 0.2s ease'
                        }}
                    >
                        <span></span>
                        <span>Add New Pet</span>
                    </Link>
                </div>
            </div>
        </div>
    );
};

export default Home; 